import Header from './components/Header/Header';
import Announcement from './components/Announcement/Announcement'
import TransactionState from './components/TransactionState/TransactionState'
import Intro from './components/Intro/Intro'
import PageRouter from './pages/PageRouter';
import { useLocation } from 'react-router-dom'
import './app.less';
import './assets/css/fonts.less'
import classNames from 'classnames';
import React, { useEffect } from 'react'
import useRpcProvider from './hooks/useRpcProvider';
import { buried } from './utils/utils'
function App() {
  const location = useLocation();
  useRpcProvider()
  const curRouterClass = location.pathname.split('/')[1]
  const trade = location.pathname.split('/')[2]
  const rootClass = classNames('deri-root', curRouterClass, trade)
  useEffect(() => {
    if (window.gtag) {
      let home = curRouterClass === "index" ? "home" : curRouterClass
      if(trade){
        home = trade
      }
      buried("screen_view", "screen_name", home)
    }
  }, [location, window.gtag])
  return (
    <div className={rootClass}>
      <Announcement />
      <Intro trade={trade} />
      <TransactionState />
      <Header></Header>
      <PageRouter></PageRouter>
    </div>
  );
}

export default App
