import * as api from "../lib/web3js/index";
import { show, hide } from "react-functional-modal";
import ChainInteraction from "../components/ChainInteraction/ChainInteraction";
import { EVENT_EXECUTOR_CANCELD, EVENT_EXECUTOR_MINED, EVENT_EXECUTOR_REQUESTED, EVENT_MSG_FILLED, EVENT_MSG_FILLING, EVENT_MSG_FILL_FAILED, EVENT_MSG_SUBMITTING, EVENT_MSG_SUBMITTTED, EVENT_MSG_SUBMITT_FAILED, EVENT_TRANS_BEGIN, EVENT_TRANS_END, MODAL_OPTIONS } from '../utils/Constants'
import Emitter from "../utils/Emitter";
import { buried } from "../utils/utils";
import WebSocket from './WebSocket';

const ws = new WebSocket(process.env.REACT_APP_EXECUTOR_WSS_URL)
class ApiProxy {
  status = 'waiting'
  async request(method, params = [], options = {}, isBuried = false) {
    const apis = await import('../lib/web3js/index')
    let res = null;
    const { subject, onAccept, onReject, onWarning, onFinish, approveTip, title, content } = options
    if (options.write) {
      params.push({
        onAccept: hash => {
          if (isBuried) {
            buried("trade_open", "trade_status", "confirm")
          }
          this.show(subject, 'success', approveTip)
          onAccept && onAccept();
          window.setTimeout(() => {
            this.close(subject)
            !options.isExecutor && Emitter.emit(EVENT_TRANS_BEGIN, { title: title, content: content, hash: hash })
            options.isExecutor && Emitter.emit(EVENT_MSG_SUBMITTING, { title: title, content: content, hash: hash })
            onFinish && onFinish()
          }, 2000)
        },
        onReceipt: () => {
          window.setTimeout(() => this.close(subject), 2000)
        },
        onReject: () => {
          this.show(subject, 'reject', approveTip)
          window.setTimeout(() => this.close(subject), 2000)
          onReject && onReject()
        },
        onWarning: () => {
          this.show(subject, 'warning', approveTip)
          onWarning && onWarning();
        }
      })
      this.show(subject, 'pending', approveTip)
    }
    res = await apis[method].call(this, ...params)
    res = this.processResponse(res, options);
    if (options.isExecutor) {
      this.listenExecutor(options, res, title, content, params[2]);
    } else {
      this.emitEvent(options, res, onFinish, title, content)
    }
    return res;
  }

  statusMonitor(event, res, title, content) {
    const { status, execute_txhash, cancel_txhash } = event;
    if (status === EVENT_EXECUTOR_REQUESTED && execute_txhash) {
      Emitter.emit(EVENT_MSG_SUBMITTTED, {
        title: title,
        content: content,
        context: { success: res.success, hash: execute_txhash },
      });
      Emitter.emit(EVENT_MSG_FILLING, {
        title: title,
        content: content,
        context: { success: true, hash: execute_txhash },
      });
    } else if (/^close/.test(status)) {
      const segments = status.split(":");
      if (segments[1].trim() === EVENT_EXECUTOR_MINED) {
        Emitter.emit(EVENT_MSG_FILLED, {
          title: title,
          content: content,
          context: { success: true, hash: execute_txhash },
        });
      } else if (segments[1].trim() === EVENT_EXECUTOR_CANCELD) {
        Emitter.emit(EVENT_MSG_FILL_FAILED, {
          title: title,
          content: content,
          context: { success: false, hash: cancel_txhash },
        });
      }
    }
  }

  listenExecutor(options, res, title, content, account) {
    if (options.write && options.includeResponse && res) {
      const hash = res.success ? res.response.data.transactionHash : res.response.transactionHash
      const error = !res.success && res.response.error
      Emitter.emit(!res.success ? EVENT_MSG_SUBMITT_FAILED : EVENT_MSG_SUBMITTTED, { title: title, content: content, context: { success: res.success, hash: hash, error: error } })
    }
    ws.subscribe('executor', { account }, event => {
      if (event && event.status) {
        console.log("executor,wsss")
        this.statusMonitor(event, res, title, content)
      }
    }, 'executor')
    // ws.onRecconnect(() => {

    // })
  }



  emitEvent(options, res, onFinish, title, content) {
    if (options.write && options.includeResponse && res) {
      const hash = res.success ? res.response.data.transactionHash : res.response.transactionHash
      const error = !res.success && res.response.error
      Emitter.emit(EVENT_TRANS_END, { title: title, content: content, context: { success: res.success, hash: hash, error: error } })
    }
  }


  syncRequest(method, params = [], options = {}) {
    const res = api[method].call(this, ...params)
    return this.processResponse(res, options)
  }

  close(subject) {
    hide(this.getMessageKey(subject))
  }

  getMessageKey(subject) {
    return `transaction-box-${subject.split(/\s+/).join('-')}`
  }

  show(subject, status, approveTip) {
    const key = this.getMessageKey(subject)
    this.close(key);
    const options = {
      ...MODAL_OPTIONS,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 1001,
      },
      key: key
    }
    show(<ChainInteraction title={subject} status={status} options={{ approveTip }} close={() => this.close(subject)} />, options)
  }



  processResponse(res, options) {
    if (options.includeResponse) {
      if (res && res.response) {
        return res
      }
    } else {
      if (res && res.response) {
        return res.response.data
      }
    }
    return res
  }
}

export default new ApiProxy();